import types from '../types.js';
import axios from 'axios';

//定义数据 vue组件 可以通过mapState进行取值 或者 this.$store.state.food进行取值
var state = {
	goods : [],
	foodShow : false,
	food : {},
	buyShow:false
}
//相当于计算属性  然后把state里面的数据 原模原样的返回出去
//然后 vue组件如果想要在mapGetters里面进行取值 就必须在此返回想要的数据
var getters = {

	goods( state ){
		return state.goods;
	},
	foodBuy(state){
		return state.foodBuy;
	},
	foodShow(state){
		return state.foodShow;
	},
	food(state){
		return state.food;
	},
	buyShow(state){
		return state.buyShow;
	}

}
var actions = {
	// 获取商家数据
	getGoods( { commit,state } ){
		axios.get('./data.json').then( (res) => {
			// console.log(res.data.data);
			// if( res.data.errno === 0 ){
						//类型 （函数命 方法名） 参数
				commit( types.GET_GOODS , res.data.goods );
			// }
		});
	},
	//打开food商品详情
	showFoodDetail( { commit } , food ){
		//  action 通过commit 将方法 提交到 mutation
		commit( types.SHOW_FOOD , food );
	},
	//关闭food商品详情
	hideFoodDetail( { commit } ){
		commit(  types.HIDE_FOOD );
	},
	showBuy({commit}){
        commit( types.showBuy);
    },
    hideBuy({commit}){
        commit(types.hideBuy);
	}
}

var mutations = {
    [types.GET_GOODS]( state , data ){
		state.goods = data;
	},
	//打开food商品详情
	[types.SHOW_FOOD]( state , f ){
		state.foodShow = true;
		state.food = f;
	},
	//关闭food商品详情
	[types.HIDE_FOOD]( state ){
		state.foodShow = false;
	},
	
    [types.showBuy](state){
		state.buyShow = true;
		console.log(state.buyShow)
    },
    [types.hideBuy](state){
        state.buyShow = false;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


