<template>
    <div class="food" v-show="foodShow">
        <div class="food-wrap">
            <div class="good-img">
                <div class="close" @click = 'hideFoodDetail'>
                    <i class="icon iconfont icon-X"></i>
                </div>
                <img :src="food.image" alt="">
            </div>
            <div class="good-info">
                <h3>{{food.name}}</h3>
                <p>月售{{food.sellCount}}份&nbsp;好评率{{food.rating}}%</p>
                <div class="new-price">
                    ￥{{food.price}}
                    <span class="old-price">
                        ￥28
                                 
                    </span>
                    <p class="add-good" v-show='!food.count' @click = 'addFirst'>
                        加入购物车
                    </p>
                    <cartControl :food = 'food' v-show='food.count' class="cart-control"></cartControl>

                </div>
                <div class="good-desc">
                    <h3>商品介绍</h3>
                    <p class="food-info">
                        {{food.info}}
                    </p>
                </div>
            </div>
            <div class="good-ratings">
                <h3>
                    商品评价
                </h3>
                <ratingSelect></ratingSelect>
                <!-- <div class="buttonwrap">
                    <div class="button all">全部(3)</div>
                    <div class="button recommend">推荐</div>
                    <div class="button bad">吐槽</div> 
                </div>
                <div class="select-ratings">
                    <i class="icon iconfont icon-duihao"></i>
                    <p class="text">
                        只看有内容的评价
                    </p>
                </div>
                <div class="ratings" v-for="(item,i) in food.ratings" :key="i">
                    <div class="time" >
                        {{item.rateTime | time}}
                    </div>
                    <div class="content">
                        <p>llllll</p>
                    </div>
                    <div class="user">
                        {{item.username}}
                    </div>
                    <div class="userimg">
                        <img :src="item.avatar" alt="">
                    </div>
                </div> -->
            </div>
            
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import cartControl from '../cartControl/cartcontrol.vue';
    import ratingSelect from '../ratingSelect/ratingSelect.vue';
    import Vue from 'vue';


    export default {
        
        computed:{
            ...mapGetters([
                'foodShow',
                'food',
                'ratings',
                'selectRatings',
                'goods'
            ])
        },
        components:{
            cartControl,
            ratingSelect
		},
        methods:{
            // 关闭商品详情
            hideFoodDetail(){
                this.$store.dispatch('hideFoodDetail');
            },
            addFirst(){
				Vue.set( this.food , 'count' , 1 );
			}
        },
        filters:{
            time:( (data)=>{
                var  data = new Date();
                return data.getFullYear()+"-"+(data.getMonth()+1)+"-"+data.getDate()+" " + data.getHours()+":"+data.getMinutes();
            } )
        }

    }
</script>

<style lang="less" scoped>
    .food{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #f3f5f7;
        overflow: scroll;
        .food-wrap{
            width: 100%;
            height: 100%;
            .good-img{
                position: relative;
                width: 100%;
                height: 0;
                padding-top: 100%;
                img{
                    position: absolute;
                    top: 0;
                    width: 100%;
                    left: 0;
                    height: 100%;
                }
                .close{
                    position: absolute;
                    top: 0.3rem;
                    left: 0.3rem;
                    width: 0.5rem;
                    height: 0.5rem;
                    color: white;
                    // background:pink;
                    z-index: 9;
                }
            }
            .good-info{
                width: 100%;
                height: 2rem;
                // border: 1px solid red;
                background: white;
                h3{
                    color: black;
                    font-size: 0.3rem;
                    font-weight: bold;
                    padding-top:0.4rem; 
                    margin-left: 0.2rem;
                }
                p{
                    font-size: 0.28rem;
                    margin-top:0.2rem; 
                    margin-left: 0.2rem;
                }
                .new-price{
                    color: red;
                    font-size: 0.4rem;
                    font-weight: bold;
                    margin-left: 0.2rem;
                    margin-top:0.2rem;                     
                    .old-price{
                        font-size: 0.24rem;
                        color: rgb(147, 153, 159);
                        font-weight: 700;
                        text-decoration: line-through;
                        
                    }
                    .add-good{
                        width: 1.5rem;
                        height: 0.5rem;
                        font-size: 0.24rem;
                        line-height: 0.5rem;
                        text-align: center;
                        text-decoration: none;
                        background: #00a0dc;
                        border-radius: 0.35rem;
                        color: white;
                        position: relative;
                        left: 5.3rem;
                        top: -0.7rem;
                    }
                    .cart-control{
                        position: relative;
                        top: -0.5rem;
                        left: 5rem;
                        width: 2rem;
                    }
                }
            }
            .good-desc{
                width: 100%;
                height: 2.5rem;
                // border: 1px solid red;
                background: white;
                .food-info{
                    color: #a3a6a9;
                    font-size: 0.24rem;
                    line-height: 0.48rem;
                }
            }
            .good-ratings{
                width: 100%;
                // height: 100%;
                // border: 1px solid red;
                position: relative;
                top: 3.5rem;
                background: white;
                h3{
                    color: black;
                    font-size: 0.3rem;
                    font-weight: bold;
                    padding-top:0.4rem; 
                    margin-left: 0.2rem;
                }
                .buttonwrap{
                    width: 100%;
                    height: 1rem;
                    border-bottom: 1px solid #ccc;
                    display: flex;
                    .button{
                        margin-top: 0.2rem;
                        margin-left: 0.2rem;
                        width: 1.2rem;
                        height: 0.6rem;
                        // border: 1px solid red;
                        font-size: 0.26rem;
                        line-height: 0.6rem;
                        text-align: center;
                    
                    }
                    .all{
                        background: #00a0dc;
                    }
                    .recommend{
                        background: #ccecf8;
                    }
                    .bad{
                        background: #e9ebec;
                    }
                }
                .select-ratings{
                    width: 100%;
                    height: 1rem;
                    border-bottom: 1px solid #ccc;
                    display: flex;
                    padding-left: 0.3rem;
                    i{
                        font-size: 0.5rem;
                        color: #ccc;
                        line-height: 1rem;
                    }
                    p{
                        color: #c3c6c9;
                        font-size: 0.32rem;
                        line-height: 1rem;
                    }
                }
                .ratings{
                    width: 100%;
                    height: 1.5rem;
                    margin-top: 0.3rem;
                    padding-left: 0.3rem;
                    border-bottom: 1px solid #ccc;
                    .time{
                        font-size: 0.28rem;
                    }
                    .content{
                        font-size: 0.3rem;
                        color: #07111b;
                        line-height: 0.8rem;
                    }
                    .user{
                        position: relative;
                        top: -1.1rem;
                        left: 5rem;
                        font-size: 0.28rem;

                    }
                    .userimg{
                        img{
                            width: 0.5rem;
                            height: 0.5rem;
                            position: relative;
                            top: -1.5rem;
                            left: 6.3rem;
                            border-radius: 0.25rem;
                        }
                        
                    }
                }
            }
            
        }
    }
</style>




