import types from '../types.js';
import axios from 'axios';

var state = {
    seller :{},
    //v-show 如果是false 不显示
    detailShow:false,
    
}

var getters = {
    seller( state ){
        return state.seller;
    },
    detailShow(state){
        return state.detailShow;
    }
}

var actions = {
    //获取商家数据
    getSeller( {commit,state} ){
        axios.get('./data.json').then( (res)=>{
            // console.log(res.data);
            // if(res.data.errno === 0){
                commit( types.GET_SELLER,res.data.seller )
            // }
        } );
    },
    showDetail({commit}){
        commit( types.showDetail);
    },
    hideDetail({commit}){
        commit(types.hideDetail)
    }
}

var mutations = {
    //获取商家数据      //接受的参数
    [types.GET_SELLER]( state,data ){
        state.seller = data;
    },
    [types.showDetail](state){
        state.detailShow = true;
    },
    [types.hideDetail](state){
        state.detailShow = false;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


