<template>
    <div class="ratingSelect">
        <div class="buttonwrap">
            <div class="button all" @click='select(2)' :class = "{'selected' : selectType === 2   }">{{types[2]}}({{ratings.length}})</div>
            <div class="button recommend" @click='select(0)' :class = "{'selected' : selectType === 0   }">{{types[0]}}({{goodCount}})</div>
            <div class="button bad" @click='select(1)' :class = "{'selected' : selectType === 1   }">{{types[1]}}({{badCount}})</div> 
        </div>
        <div class="select-ratings" @click='check()'>
            
            <i class="icon iconfont icon-duihao" :class = "{'checked' : onlyContent  }"></i>
            <p class="text" :class = "{'checked' : onlyContent  }">
                只看有内容的评价
            </p>
        </div>
        <div >
            <div class="rating-detail" v-for="(item, i ) in selectRatings" :key="i">
                <div class="userimg">
                    <img :src="item.avatar" alt="">
                </div>
                <div class="username">
                    {{item.username}}

                </div>
                <star :score = 'item.score' :size = " 'small' " class="rating-star" ></star>
                <p>{{item.deliveryTime}}分钟送达</p>
                <div class="rating-time">
                    {{item.rateTime | time}}
                </div>
                <div class="content">
                    <i class="icon iconfont icon-damuzhion" v-if="item.rateType === 0 "></i>
                    <i class="icon iconfont icon-down" v-if="item.rateType === 1 "></i>
                    &nbsp;{{item.text}}
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import { mapGetters } from 'vuex';
    import star from "../star/star.vue"


    export default {
         created(){
            this.$store.dispatch('getRatings');
            this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
            // this.menuBS();s
        },
        computed:{
            ...mapGetters([
                'types',
                'seller',
                'ratings',
                'selectRatings',
                "onlyContent",
                'selectType'
            ]),
            goodCount(){
                return this.ratings.filter( rating=>{
                    return rating.rateType === 0;
                }).length;
            },
            badCount(){
                return this.ratings.filter( rating=>{
                    return rating.rateType === 1;
                }).length;
            }
        },
         components:{
            star
        },
        filters:{
            time:( (data)=>{
                var  data = new Date();
                return data.getFullYear()+"-"+(data.getMonth()+1)+"-"+data.getDate()+" " + data.getHours()+":"+data.getMinutes();
            } )
        },
        methods:{
            select(num){
                this.$store.dispatch('select',num);
            },
            check(){
                this.$store.dispatch('check');
            },
            menuBS(){ 
                let ratingscroll = new BScroll(this.$refs.ratingScroll,{
                    click:true
                });
            }
        },
        
    }
</script>


<style lang="less" scoped>
    .ratingSelect{
        width: 100%;
        height: 7rem;
        overflow: hidden;
        overflow: scroll;
        .buttonwrap{
            width: 100%;
            height: 1rem;
            border-bottom: 1px solid #ccc;
            display: flex;
            .button{
                margin-top: 0.2rem;
                margin-left: 0.2rem;
                width: 1.2rem;
                height: 0.6rem;
                    // border: 1px solid red;
                font-size: 0.26rem;
                line-height: 0.6rem;
                text-align: center;
                
            }
            .all{
                background: #ccecf8;
                &.selected{
                    background: #00a0dc;                    
                }
            }
            .recommend{
                background: #ccecf8;
                &.selected{
                    background: #00a0dc;                    
                }
            }
            .bad{
                background: #ccecf8;
                &.selected{
                    background: #00a0dc;                    
                }
            }
        }
        .select-ratings{
            width: 100%;
            height: 1rem;
            border-bottom: 1px solid #ccc;
            display: flex;
            padding-left: 0.3rem;
            
            
            i{
                font-size: 0.5rem;
                color:#ccc;
                &.checked{
                    color: #00a0dc;
                }
                line-height: 1rem;
            }
            p{
                color: #c3c6c9;
                &.checked{
                    color: #00a0dc;
                }
                font-size: 0.32rem;
                line-height: 1rem;
            }
        }
        .rating-detail{
            background: white;
            border-bottom: 1px solid #ccc;
            
            .userimg img{
                width: 0.56rem;
                border-radius: 0.28rem;
                margin-top: 0.2rem;
                margin-left: 0.2rem;
            }
            .username{
                font-size: 0.28rem;
                position: relative;
                top: -0.5rem;
                left: 0.9rem;
            }
            .rating-star{
                position: relative;
                top: -0.6rem;
                left: 0.9rem;
            }
            p{
                font-size: 0.24rem;
                position: relative;
                top: -0.9rem;
                left: 2.3rem;
            }
            .rating-time{
                font-size: 0.3rem;
                position: relative;
                top: -1.45rem;
                left: 4.8rem;
            }
            .content{
                width: 6.2rem;
                font-size: 0.34rem;
                position: relative;
                top: -0.9rem;
                left: 0.8rem;
            }
        }
    }
</style>





