<template>
  <div class="goods">
      <div class="menu-wrap" ref="menuScroll">
          <ul>
              <!-- {{currentIndex}} -->
              <li v-for="(item,i) in goods" @click = 'selectMenu(i)' :key="i" class='menu-item' :class="{'current' : i === currentIndex}">
                 <span class="text">{{item.name}}</span>
              </li>
          </ul>
      </div>
      <div class="food-wrap" ref="menuScroll2">
          <div>
                <div class="food food-list-hook" v-for="(item,i) in goods" :key="i">
                    <div class="food-type" >
                        {{item.name}}
                    </div>
                    <div class="food-detail" v-for="(food,i) in item.foods" :key="i">
                        <img :src='food.icon 'alt="" @click='showFoodDetail(food)'>
                        <h3 class="food-name">{{food.name}}</h3>
                        <span class="type">口味：{{food.description}}</span>
                        <p class="sale-number">月售{{food.sellCount}}份&nbsp;好评率{{food.rating}}%</p>
                        <div class="new-price">
                            ￥{{food.price}}
                            <span class="old-price">
                                ￥28
                            </span>
                            <div class="cart-wrap">
                                <cartControl :food = 'food' ></cartControl>
                                 <!-- <i class="icon iconfont icon-jiahao" v-show="!good.count"  @click="showCartcontrol"></i> -->
                            </div>
                            
                           
                        </div>
                       
                    </div>
                    
                </div>
          </div>
      </div>
      <food-detail></food-detail>
      
  </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import BScroll from 'better-scroll';
    import foodDetail from '../foodDetail/foodDetail.vue';
    import cartControl from '../cartcontrol/cartcontrol.vue';


    export default {
        
        data(){
            return{
                listHeight:[],//定义左边菜单对应的每个右边的菜品的高度
                scrollY:0//定义鼠标往上滚动的高度
            }
        },
        components:{
            foodDetail,
            cartControl
        },
        created(){
            this.$store.dispatch('getGoods');
            this.$nextTick( () =>{
                    this.menuBS();
                } );
        },
        computed:{
            ...mapGetters([
                    'goods',
                ]),
            currentIndex(){
                
                for( let i = 0;i<this.listHeight.length;i++ ){
                   
                    let height1 = this.listHeight[i];
                    let height2 = this.listHeight[i+1];
                    // console.log(i);
                    // console.log(this.listHeight[i]);
                    // console.log(height1,"=====",height2);
                    if(this.scrollY>= height1 && this.scrollY < height2){
                        return i ;
                    }
                    //如果滚动的高度在某个菜单高度和下一个菜单高度之间，name返回某个菜单的索引
                    //左边停留在那个菜单索引所在的位置           
                } 
                return 0;
                    //如果没有滚动 ，那么默认停留在第一个菜单索引的位置
            }
        },
        //定义方法 
        methods:{
            menuBS(){
                this.menuscroll = new BScroll(this.$refs.menuScroll,{
                    click:true
                });

                // if(!this.menuScroll2){
                //     this.menuscroll2 = new BScroll(this.$refs.menuScroll2,{
                //         click:true,
                //         probeType:3
                //     });
                //     this.menuscroll2.on('scroll',(pos)=>{
                //         this.scrollY = Math.abs( Math.ceil(pos.y) )+2;
                //         // console.log(this.scrollY);
                //     })
                // }else{
                //     this.menuscroll2.refresh();
                // }
                
            },
            calcHeight(){
                let foodList = this.$refs.menuScroll2.getElementsByClassName('food');
                let height = 0;
                this.listHeight.push(height);
                for(let i= 0;i<foodList.length;i++){
                    height += foodList[i].clientHeight;
                    this.listHeight.push(height);
                    // console.log(height)
                }
                    
            },
            selectMenu(i){

                let foodList = this.$refs.menuScroll2.getElementsByClassName('food-list-hook');
                // console.log(foodList)
                let el = foodList[i];
                this.menuscroll2.scrollToElement(el,100);
            },
            //打开food商品详情
            showFoodDetail(f){
                // 你在组件中使用 this.$store.dispatch('xxx') 分发 action，
                //或者使用 mapActions 辅助函数将组件的 methods 映射为 store.dispatch 调用
                //（需要先在根节点注入 store）
                this.$store.dispatch('showFoodDetail',f);
            }
            

        },
        //监听事件
        watch:{
            goods(){
                this.$nextTick( () =>{
                    this.menuBS();
                    this.calcHeight();
                    if(!this.menuScroll2){
                        this.menuScroll2 = new BScroll (this.$refs.menuScroll2,{
                            click:true,
                            probeType:3
                        })
                    }else{
                        this.menuscroll2.refresh();
                    }
                    if(!this.menuScroll){
                        this.menuScroll = new BScroll (this.$refs.menuScroll,{
                            click:true,
                        })
                    }else{
                        this.menuScroll.refresh();
                    };
                //     this.calcHeight();
                } );
                
            }
        }
    }
</script>

<style lang="less" scoped>
    .goods{
        width: 100%;
        display: flex;
        font-size: 24px;
        position: absolute;
        top: 3.52rem;
        bottom: 0.92rem;
        overflow: hidden;
        .menu-wrap{
            flex: 0 0 1.6rem;
            width: 1.6rem;
            background: #f3f5f7;
            .menu-item{
                display: table;
                height: 1.08rem;
                font-size: 0.24rem;
                color:#07111b;
                padding: 0 0.24rem;
                width: 1.12rem;
                 &.current{
					color:red;
					background: rgba(7,17,27,0.1);
				}
                .text{
                    display: table-cell;
                    vertical-align: middle;
                    line-height: 0.28rem;
                    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                }
            }
        }
        .food-wrap{
           
            flex: 1;
            .food-type{
                height: 0.5rem;
                font-size: 0.24rem;
                line-height: 0.5rem;
                border-left: 5px solid #ccc;
                padding-left: 0.2rem;
                background: #f3f5f7;
            }
            .food-detail{
                width: 5.9rem;
                height: 2.2rem;
                // display: flex;
                // justify-content: space-between;
                box-sizing: border-box;
                padding-top: 0.36rem;
                padding-left: 0.36rem;
                border-bottom: 1px solid #ccc;
                img{
                    margin-top: 0.1rem;
                    width: 1.15rem;
                    height: 1.15rem;
                }
                .food-name{
                    font-size: 0.28rem;
                    color: rgb(7, 17, 27);
                    position: relative;
                    top: -1.2rem;
                    left: 1.3rem;
                }
                .type{
                    font-size: 0.2rem;
                    position: relative;
                    top: -1.3rem;
                    left: 1.3rem;
                    overflow: hidden;
                }
                .sale-number{
                    font-size: 0.2rem;
                    position: relative;
                    top: -1.3rem;
                    left: 1.3rem;
                }
                .new-price{
                    color: red;
                    font-size: 0.28rem;
                    position: relative;
                    top: -1.2rem;
                    left: 1.25rem;
                    font-weight: bold;
                    .old-price{
                        font-size: 0.2rem;
                        color: rgb(147, 153, 159);
                        font-weight: 700;
                        text-decoration: line-through;
                        
                    }
                   
                    .cart-wrap{
                        // border: 1px solid red;
                        width: 1.5rem;
                        height: 0.5rem;
                        position: absolute;
                        top: -0.2rem;
                        left: 2.5rem;
                        i{
                            color: #00a0dc;
                        }
                                       
                    }
                }
            }
            
        }
    }
</style>



