<template>
  <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
      <div class="detail" v-show='detailShow' >
          <div class="bg"></div>
          <div class="top" @click="hideDetail">
              <p >
              {{seller.name}}
              </p>
              <div class="star">
                  <star :score = 'seller.score' :size = " 'big' "></star>
              </div>
              <div class="decideline">
                  —————— <span>优惠信息</span> ——————
              </div>
          </div>
          <div class="saleinfo">
              <ul >
                  <li v-for="(item,i) in seller.supports" :key="i">
                      <p>
                        {{item.description}}  
                      </p>
                     
                  </li>
                  <li><img src="../../assets/imgs/decrease.png" alt=""></li>
                  <li><img src="../../assets/imgs/discount.png" alt=""></li>
                  <li><img src="../../assets/imgs/guarantee.png" alt=""></li>
                  <li><img src="../../assets/imgs/guarantee.png" alt=""></li>
                  <li><img src="../../assets/imgs/invoice.png" alt=""></li>
              </ul>
              <div class="decideline2">
                  —————— <span>商家公告</span> ——————
              </div>
          </div>
          <div class="sellerinfo">
              <p>
                  {{seller.bulletin}}
              </p>
          </div>
          <div class="bottom" @click="hideDetail">
              X
          </div>
          

      </div>
  
  </transition>
</template>

<script>
    import {mapGetters} from 'vuex';
    import animate from 'animate.css';
    import star from '../../components/star/star.vue';

    export default {
        props:['name'],
        computed:{
            ...mapGetters([
                'seller',
                'detailShow'
            ])
        },
        methods:{
            hideDetail(){
                this.$store.dispatch('hideDetail');
            }
        },
        components:{
            star
        }
    }
</script>

<style lang="less" scoped>
    .detail{
        font-size: 0.32rem;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 555;
        // background: rgba(0, 0, 0, 0.7);
        color: white;
        text-align: center;
        .bg{
            background: #000;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background:rgba(7, 17, 27, 0.8);
            filter: blur(10px);
            z-index: -1;
        }
        .top{
            box-sizing: border-box;
            height: 3rem;
            // border-bottom: 1px solid red;
            padding-top: 1.28rem;
            text-align: center;
            .star{
                margin-top: 0.24rem;
            }
            .decideline{
                position: absolute;
                top: 2.7rem;
                left: 1.2rem;
                color: rgba(255,255,255,0.2);
                span{
                    color: white;
                    font-weight: bold;
                }
            }
        }
        .saleinfo{
            box-sizing: border-box;
            // border: 1px solid red;
            width: 100%;
            height: 3.6rem;
            padding-top: 0.48rem;
            ul li{
                margin-bottom: 0.24rem;
                text-align: left;
                margin-left: 1.3rem;
                font-size: 0.28rem;
                p{
                    position: relative;
                    left: 0.5rem;;
                }
                img{
                    width: 0.4rem;
                    position: relative;
                    top: -3rem;
                    margin-bottom: -0.5rem;
                    // margin-right: -2rem;
                }
            }
            .decideline2{
                color: rgba(255,255,255,0.2);                
                position: relative;
                top: -2.2rem;
                span{
                    color: white;
                }
            }
        }
        .sellerinfo{
            margin: 0.48rem 0.24rem;
            p{
                width: 5.5rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: white;
                line-height: 0.48rem;
                padding-left: 0.7rem;
                text-align: left;
            }
        }
        .bottom{
            font-size: 0.5rem;
            font-weight: bold;
            position: absolute;
            bottom: 0.5rem;
            left: 50%
        }
    }
</style>


