<template>
  <div class="cart">
      <div class="reduce" @click = 'reduce()' :class="{'reduce-item' : !food.count}">
          <i class="icon iconfont icon-jianhao"  ></i>
      </div>
      <div class="count" :class="{'reduce-item' : !food.count}">{{food.count}}</div>
      <div class="add" @click = 'add()'>
          <i class="icon iconfont icon-jiahao" ></i>
      </div>
  </div>
</template>
<script>
   	import Vue from 'vue';

    export default {
       props:[
           'food'
       ],
       methods:{
           add(){
               console.log(111);
               if(!this.food.count){
                   Vue.set( this.food,'count',1);
               }else{
                   this.food.count++;
               }
            //    this.$store.dispatch("foodBuyArray",this.food);
            
           },
           reduce(){
               console.log(222);
               
               if(this.food.count){
                   this.food.count--;
               }
           }
       }

   }
</script>


<style lang="less" scoped>
    .cart{
       display: flex;
       color: #00a0dc;
       font-size: 0.4rem;
       padding-left: 0.5rem;
       position: relative;
       top: 0.1rem;
       line-height: 0.5rem;
    //    border: 1px solid red;
       .reduce{
           flex: 1;
           &.reduce-item{
               display: none;
           }
       }
       .count{
           flex: 1;
           color: black;
           font-size: 0.28rem;
           &.reduce-item{
               display: none;
           }
       }
       .add{
           flex: 1;
           
       }
    }
</style>




