<template>
  <div class="shop">
      <div class="pay-wrap">
            <div class="logo" :class = "{'logostyle' : totalCount > 0 }">
                <i class="icon iconfont icon-gouwuche" @click="showBuy"></i>
                <div class="number">
                    {{totalCount}}
                </div>
                
            </div>
            <div class="price">
                ￥{{totalPrice}}
            </div>
            <p>另需配送费￥4元</p>
            <div class="pay" :class = "{'topay' : totalPrice >= 20 }">
                {{pay}}
            </div>
      </div>
      <buy ></buy>
     
    
  </div>
</template>


<script>
    import { mapGetters,mapActions } from 'vuex';
    import buy from '../buy/buy.vue'
	export default {
		components:{
            buy
        },
         created(){
            this.$store.dispatch('getGoods');
        },
		computed : {
			...mapGetters([
                'seller',
                'goods'
			]),
			// 筛选所有由count的商品
			selectFoods(){
				let  foods = [];
				this.goods.forEach( good => {
					good.foods.forEach( food => {
						if( food.count > 0 ){
							foods.push( food );
						}
					})
				});
                return foods;
                console.log(foods);
			},
			// 计算购物车商品的总数量
			totalCount(){
				let count = 0 ;
				this.selectFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
			//计算购物车商品总价
			totalPrice(){
				let price = 0;
				this.selectFoods.forEach( food => {
					price += food.count * food.price;
				});
				return price;
			},
			//结算描述
			pay(){

				if( this.totalPrice === 0 ){
					return `¥ ${this.seller.minPrice} 元起送`;
				}else if ( this.totalPrice < this.seller.minPrice ) {
					return `还差¥ ${this.seller.minPrice - this.totalPrice } 元起送`
				}else{
					return "去结算";
				}
            }
        },
        methods:{
            ...mapActions([
                'showBuy'
            ])
		}
    }
</script>

<style lang="less" scoped>
   .shop{
       width: 100%;
       height: 0.9rem;
    //    border: 1px solid red;
       position: absolute;
       bottom: 0;
       background: #141d27;
       .pay-wrap{
        //    z-index: 1000;
           .logo {
                width: 0.8rem;
                height: 0.8rem;
                background: #2b343c;
                text-align: center;
                border: 5px solid #141d27;
                border-radius: 0.5rem;
                position: relative;
                left: 0.2rem;
                top: -0.2rem;
                z-index: 2;
                &.logostyle{
                    background: #00a0dc;
                    i{
                        color: white;
                    }     
                }
                i{
                font-size: 0.48rem;
                line-height: 0.8rem;
                color: #80858a;
                    }
                .number{
                    background: red;
                    width: 0.3rem;
                    height: 0.3rem;
                    color: white;
                    font-size: 0.26rem;
                    line-height: 0.3rem;
                    font-weight: 700;
                    position: relative;
                    top: -0.9rem;
                    left: 0.5rem;
                    border-radius: 0.2rem;
                }
            }
            .price{
                border-right: 1px solid rgba(255, 255, 255, 0.4);
                width: 1rem;
                height: 0.7rem;
                position: relative;
                top: -0.9rem;
                left: 1.5rem;
                font-size: 0.34rem;
                color: rgba(255, 255, 255, 0.4);
                font-weight: 700;
                line-height: 0.7rem;
            }
            p{
                width: 2.7rem;
                position: relative;
                top: -1.4rem;
                left: 2.6rem;
                font-size: 0.34rem;
                color: rgba(255, 255, 255, 0.4);
            }
            .pay{
                position: relative;
                top: -2.05rem;
                left: 5.4rem;
                width: 2.1rem;
                height: 1rem;
                background: #2b333b;
                color: rgba(255, 255, 255, 0.4);
                font-size: 0.28rem;
                font-weight: 700;
                line-height: 0.9rem;
                text-align: center;
                &.topay{
                        background: #00a0dc;
                        color: white;
                    
                }
            }
       }
    
       
   }
</style>




