<template>
    <transition enter-active-class="animated bounceInBottom" leave-active-class="animated bounceOutBottom">
        <div class="buy" v-show='buyShow'>
            <div class="buytop">
                <div class="clear" @click="clearCart()">
                    清空购物车
                </div>
                <div class="close" @click="hideBuy">
                    关闭
                </div>
            </div>
            <div class="buygoods" v-for="(foods,i) in selectFoods" :key="i">
                <div class="goodname">
                    <p>{{foods.name}}</p>
                </div>
                <cartControl :food ='foods' ></cartControl>
                <div class="price">
                    ￥{{foods.price}}
                </div>
            </div>
        </div>
    </transition>
  
</template>

<script>
    import {mapGetters} from 'vuex';
    import animate from 'animate.css';
    import cartControl from '../cartControl/cartcontrol.vue';
    import Vue from 'vue';

    export default {
        props:['name'],
        created(){
            this.$store.dispatch('getGoods');
        },
        methods:{
            hideBuy(){
                this.$store.dispatch('hideBuy');
            },
            clearCart(){
                this.selectFoods.forEach( food=>{
                    food.count=0;
                } )
            }
        },
        components:{
            cartControl
		},
        computed:{
            ...mapGetters([
                'goods',
                'buyShow',
                'food'
            ]),
            selectFoods(){
				let  foods = [];
				this.goods.forEach( good => {
					good.foods.forEach( food => {
						if( food.count > 0 ){
							foods.push( food );
						}
					})
				});
				return foods;
            }
        }
    }
</script>



<style lang="less" scoped>
    .buy{
        width: 100%;
        height: 3rem;
        overflow-y: scroll;
        // overflow-y: hidden;
        // border: 1px solid red;
        position: absolute;
        bottom:0.9rem;
        left: 0;
        right: 0;
        z-index: 1;
        background: #f3f5f7;
        // opacity: 0.9;
        font-size: 0.3rem;
        padding-top: 0.2rem;
        padding-left: 0.1rem;
        padding-right: 0.1rem;
        padding-bottom: 0.5rem;
        box-sizing: border-box;
        // position: relative;    
        .buytop{
            border-bottom: 1px solid #ccc;
            .close{
                width: 0.8rem;
                position: absolute;
                top: 0.2rem;
                right: 0;
                // border: 1px solid blue;
            }
        }  
        .buygoods{
            background: white;
            width: 100%;
            height: 1rem;
            line-height: 1rem;
            display: flex;
            border-bottom: 1px solid #ccc;
            .goodname{
                padding-left: 0.2rem;
                
            }
            .price{
                position: absolute;
                left: 3rem;
                color: red;
            }
        }  
        
    }
</style>



