<template>
  <div class="ratings">
      <div class="score">
          <div class="total-score">
            {{seller.score}}
             <p>综合评分</p>
             <span>高于周边商家{{seller.rankRate}}%</span> 
          </div>
          <div class="score-detail">
              <div class="serve1">
                  服务态度：
                <star :score = 'seller.serviceScore' :size = " 'big' " class="serve1star"></star>

              </div>
              <div class="serve2">
                  服务态度：
                <star :score = 'seller.foodScore' :size = " 'big' " class="serve2star"></star>

              </div>
              <div class="deliver-time">
                  送达时间：
                  <p>{{seller.deliveryTime}}分钟</p>
              </div>
          </div>
          
      </div>
        <div class="user-ratings" >
            <ratingSelect></ratingSelect>
           
        </div>
  </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import star from '../../components/star/star.vue';
    import ratingSelect from '../ratingSelect/ratingSelect.vue';


    export default {
        props:['goods'],
        created(){
            this.$store.dispatch('getRatings');
            this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
            // this.menuBS();s
        },
        computed:{
            ...mapGetters([
                'types',
                'seller',
                'ratings',
                'selectRatings'
            ])
        },
        components:{
            star,
            ratingSelect
        }
        // watch:{
        //     ratings(){
        //         this.$nextTick( () =>{
        //             this.menuBS();
                    
        //         } );
        //     }
        // }
    }
</script>

<style lang="less" scoped>
    .ratings{
        width: 100%;
        height: 9rem;
        // border: 1px solid red;
        background: #f3f5f7;
        
        .score{
            width: 100%;
            height: 2.1rem;
            background: white;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc; 
            display: flex;           
            .total-score{
                margin-top: 0.4rem;
                width: 2.7rem;
                height: 1.3rem;
                color: #ff9900;
                font-size: 0.48rem;
                border-right: 1px solid #ccc;
                text-align: center;
                p{
                    color: black;
                    font-size: 0.24rem;
                    text-align: center;
                }
                span{
                    font-size: 0.24rem;
                    color: #b4b8bb;
                }
            }
            .score-detail{
                font-size: 0.24rem;
                margin-left: 0.4rem;
                .serve1{
                    margin-top: 0.5rem;
                    margin-bottom: 0.2rem;
                    position: relative;                    
                    .serve1star{
                        width: 3rem;;
                        position: absolute;
                        top: -0.2rem;
                        left: 1.2rem;
                    }
                }
                .serve2{
                    // margin-top: 0.5rem;
                    margin-bottom: 0.2rem;
                    position: relative;                    
                    
                    .serve2star{
                        width: 3rem;;
                        position: absolute;
                        top: -0.2rem;
                        left: 1.2rem;
                    }

                }
                .deliver-time{
                    p{
                        position: relative;
                        top: -0.25rem;
                        left: 1.2rem;
                        color: #93999f;
                    }
                }
            }     
            
        }
        // .user-ratings{
           
        //     // .ratings-item{
        //     //     font-size: 0.28rem;
        //     // }
        // }
        
    }
</style>




