import types from '../types.js';
import axios from 'axios';
import good from './goods.js';



var state = {
	ratings:[],
	types:['满意','不满意','全部'],
	selectRatings:[],
	selectType:2,//默认一开始显示全部评论
	onlyContent:false,//显示有内容的评论
}

var getters = {
    ratings( state ){
		return state.ratings;
	},
	types(state){
		return state.types;
	},
	selectRatings(state){
		return state.selectRatings;
	},
	selectType(state){
		return state.selectType;
	},
	onlyContent(){
		return state.onlyContent;
	}
}

var actions = {
	//获取评价
    getRatings( { commit,state } ){
		axios.get('./data.json').then( (res) => {
			// console.log(res.data)
			// if( res.data.errno === 0 ){
						//类型 （函数命 方法名） 参数
				commit( types.GET_RATINGS , res.data.ratings );
			// }
		});
	},
	//初始化页面
	changeTypes( {commit}, _types ){
		commit (types.CHANGE_TYPES,_types);
	},
	//筛选要显示的评论类型（0，1，2）
	select( { commit } , num ){
		console.log(num)
		commit(types.TYPES,num)
	},
	//显示有内容的评价
	check({commit}){
		// console.log("a");
		commit(types.CHECK);
	}

	
}

var mutations = {
	//获取评价
    [types.GET_RATINGS]( state , data ){
		state.ratings = data;
	},
	//初始化页面
	[types.CHANGE_TYPES](state,_types){
		state.types= _types;
		state.selectRatings= state.ratings;
	},
	//筛选要显示的评论类型
	[types.TYPES](state,num){
		state.selectType = num;
		// console.log(state.ratings)
		state.selectRatings = state.ratings.filter(rating =>{

			if(state.selectType===2){
				return true;
			}else{
				return rating.rateType == state.selectType;				
			}
		})
	},
	//查看有内容的评论
	[types.CHECK](state){
		// console.log("b");
		
		state.onlyContent = !state.onlyContent;
		state.selectRatings =state.ratings.filter(rating =>{
			//点击了显示有内容的评论 且 评论内容为空
			if(state.onlyContent && !rating.text){
				return false;
			}else if(state.selectType ===2){
				return true
			}else{
				return rating.rateType == state.selectType;
			}
		})
	}
}

export default {
    state,
    getters,
    actions,
    mutations
}


