<template>
  <div class="header">
      <div class="bg">
				<img src="../../assets/imgs/bg.png" alt="" > 
	  </div>
      <!-- <img src="../../assets/imgs/bg.png" alt=""> -->
      <div class="title">
          <div class="sellerpic">
              <img src="../../assets/imgs/bg.png" alt="pic" @click="showDetail">
          </div>
          <div>
              <p class="sellername">
                  <img src="../../assets/imgs/brand.png" alt="">
                  {{seller.name}}
              </p>
              <span>{{seller.description}}/38分钟送达</span>
              <div class="decrease">
                  <img src="../../assets/imgs/decrease.png" alt="">
                  <p class="sale" v-if="seller.supports">{{seller.supports[0].description}}，满50减10</p>
                  
              </div>
          </div>
          <div class="number" v-if="seller.supports">
              {{seller.supports.length}}个 &nbsp;>
          </div>
          
      </div>
      <div class="inform">
          <img src="../../assets/imgs/bulletin.png" alt="">
          <p>{{seller.bulletin}}</p>
      </div>
      
  </div>
</template>
<script>
    import {mapGetters ,mapActions} from 'vuex';
    export default {
        created(){
            this.$store.dispatch('getSeller');
        },
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        methods:{
            ...mapActions([
                'showDetail'
            ])
        }
    }
</script>

<style lang="less" scoped>
    .header{
        width: 100%;
        .bg{
            position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: -2;
			filter:blur(6px);
			img{
				width: 100%;
				height: 2.5rem;
			}
        }
        .title{
            box-sizing: border-box;
            background: rgba(7, 17, 27, 0.5);
            height: 2.12rem;
            padding: 0.48rem 0.24rem 0.36rem 0.48rem;
            
            // display: flex;
            .sellerpic img{
                position: absolute;
                top: 0.48rem;
                left: 0.48rem;
                width: 1.28rem;
                height: 1.28rem;
            }
            .sellername{  
                position: absolute;
                top: 0.48rem;
                left: 2rem;
                color: white;
                font-size: 0.32rem;
                font-weight: bold;
                line-height: 0.36rem;
                img{
                    height: 0.35rem;
                    
                }
            }
            span{
                position: absolute;
                top: 1rem;
                left: 2rem;
                font-size: 0.24rem;
                color: white;
                font-weight: 200;
                line-height: 0.24rem;
            }
            .decrease{
                position: absolute;
                top: 1.44rem;
                left: 2rem;
                font-size: 0.2rem;
                color: white;
                font-weight: 200;
                line-height: 0.24rem;
                img{
                    width: 0.24rem;
                    position: absolute;
                    left: 0rem;
                }
                .sale{
                   position: relative;
                   left:0.3rem;
                }
            }
            .number{
                position: absolute;
                top: 1.4rem;
                right:0.24rem ;
                line-height: 0.48rem;
                height: 0.48rem;
                width: 1rem;
                border-radius: 0.24rem;
                font-size: 0.2rem;
                color: white;
                background-color: rgba(0,0, 0, 0.2);
                text-align: center;
            }
        }
        .inform{
            width: 100%;
            height: 0.56rem;
            background: rgba(7, 17, 27,0.2);
            padding-left: 0.24rem;
            padding-right: 0.24rem;
            display: flex;
            
            img{
               height: 0.35rem;;
               margin-top: 0.1rem;
            };
            p{
                width: 6rem;
                height: 0.56rem;
                font-size:0.2rem;
                font-weight: 200;
                line-height: 0.56rem;
                color:rgb(255, 255, 255);
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }
        }
    }
    
</style>




