<template>
  <div class="seller" >
        <div class="sellerinfo" >
            <div class="sellername">
                {{seller.name}}
            </div>
            <star :score = 'seller.score' :size = " 'big' " class="star" ></star>
            <div class="saledata">
                (661)&nbsp;&nbsp;月售{{seller.sellCount}}单
            </div>
            <div class="heart" >
                <i class="icon iconfont icon-xin"></i>
                <p>
                    收藏
                </p>
            </div>
        </div>
        <div class="payinfo">
            <div class="minPrice">
                <p>起送价</p> 
                <span>{{seller.minPrice}}元</span>
            </div>
            <div class="deliveryPrice">
                <p>商家配送</p> 
                <span>{{seller.deliveryPrice}}元</span>
            </div>
            <div class="deliveryTime">
                <p>平均配送时间</p> 
                <span>{{seller.deliveryTime}}元</span>
            </div>
        </div>
        <div class="activity">
            <h1>公告与活动</h1>
            <p>
                {{seller.bulletin}}
            </p>
            <ul >
                <li v-for="(item,i) in seller.supports" :key="i">
                    <p class="act-text"> {{item.description}}</p>
                    
                </li>
                <img src="../../assets/imgs/decrease.png" alt="" class="pic1">
                <img src="../../assets/imgs/discount.png" alt="" class="pic2">
                <img src="../../assets/imgs/guarantee.png" alt="" class="pic3">
                <img src="../../assets/imgs/guarantee.png" alt="" class="pic4">
                <img src="../../assets/imgs/invoice.png" alt="" class="pic5">
            </ul>
        </div>
        <div class="view">
            <h1>商家实景</h1>
            <div class="imgwrap" ref='menuWrap'>
                <ul ref="picList">
                    
                    <li v-for="(item,i) in seller.pics" :key="i" class="pic-item">
                        <img :src="item" alt="">
                    </li>
                
                </ul>
            </div>
            
            
                
        </div>
        <div class="info">
            <h1>商家信息</h1>
            <ul>
                <li v-for="(item,i) in seller.infos" :key="i" class="info-item">
                    {{item}}
                </li>
            </ul>
        </div>
  </div>
</template>


<script>
    import {mapGetters} from 'vuex';
    import star from "../star/star.vue"
	import BScroll from 'better-scroll';

    
    export default {
        computed:{
            ...mapGetters([
                'goods',
                'seller',
                'ratings'
            ])
        },
        components:{
            star
        },
        watch:{
 			seller(){
 				let picWidth = 120;
	          	let margin = 6;
	          	let width = (picWidth + margin) * this.seller.pics.length - margin;
	          	this.$refs.picList.style.width = width + 'px';

 				if( !this.picBScroll ){
	 				this.picBScroll = new BScroll( this.$refs.menuWrap , {
	 					scrollX: true,
	 				})
	 			}else{
	 				this.picBScroll.refresh();
	 			}
 			}
 		}
    }
     
</script>

<style lang="less" scoped>
    .seller{
        width: 100%;
        height: 9rem;
        // border: 1px  solid red;
        overflow-y: scroll;
        overflow-x: hidden;
        background: #f3f5f7;
        .sellerinfo{
            width: 100%;
            height:1.53rem;
            background: white;
            border-top: 1px solid #f3f5f7;
            border-bottom: 1px solid #f3f5f7;
            
            .sellername{
                font-size: 0.3rem;
                margin-top: 0.3rem;
                margin-left: 0.3rem;
            }
            .star{
                margin-left: 0.3rem;
                margin-top: 0.1rem;
            }
            .saledata{
                font-size: 0.24rem;
                position: relative;
                top: -0.4rem;
                left: 3.4rem;
            }
            .heart{
                position: relative;
                top: -1rem;
                left: 6.5rem;
                font-size: 0.3rem;
                i{
                    font-size: 0.6rem;
                    color: #ccc;
                }

            }
        }
        .payinfo{
            width: 100%;
            height: 1.5rem;
            background: white;
            // border: 1px solid red;
            display: flex;
            text-align: center;
            .minPrice{
                flex:1;
                padding-top:0.5rem;
                font-size: 0.26rem;
                border: 1px solid #ccc;
                color: #a4a9ae;
                span{
                    font-size: 0.4rem;
                    color:black;
                
                }            
            }
            .deliveryPrice{
                flex:1;
                padding-top:0.5rem;
                font-size: 0.26rem;
                border: 1px solid #ccc;
                color: #a4a9ae;
                span{
                    font-size: 0.4rem;
                    color:black;
                    
                }
            }
            .deliveryTime{
                flex:1;
                padding-top:0.5rem;
                font-size: 0.26rem;
                border: 1px solid #ccc;
                color: #a4a9ae;
                span{
                    font-size: 0.4rem;
                    color:black;
                    
                }
            }
        }
        .activity{
            width: 100%;
            // height: 100%;
            // border: 1px solid red;
            background: white;
            margin-top: 0.5rem;
        
            h1{
                font-size: 0.32rem;
                padding-top: 0.5rem;
                margin-left: 0.3rem;
                margin-bottom: 0.2rem;
            }
            p{
                font-size: 0.26rem;
                color: rgb(240, 20, 20);
                width: 6.5rem;
                margin-left: 0.3rem;
                line-height: 0.4rem;
                padding-bottom: 0.3rem;
                border-bottom: 1px solid #ccc;
            }
            ul {
                li{
                
                    line-height: 0.4rem;
                    height: 1rem;
                    margin-top: 0.5rem;
                    margin-left: 0.4rem;
                    width:6.5rem ;
                    border-bottom: 1px solid #ccc;
                    .act-text{
                        font-size: 0.3rem;
                        font-weight: 200;
                        color: rgb(7, 17, 27);
                        margin-left: 1rem;
                        border: none;
                    }
                }
                .pic1{
                    position: relative;
                    top: -7.3rem;
                    left:0.5rem ;
                }
                 .pic2{
                    position: relative;
                    top: -5.8rem;
                    left:-0.3rem ;
                }
                 .pic3{
                    position: relative;
                    top: -4.2rem;
                    left:-1.05rem ;
                }
                 .pic4{
                    position: relative;
                    top: -2.7rem;
                    left:-1.8rem ;
                }
                 .pic5{
                    position: relative;
                    top: -1.2rem;
                    left:-2.6rem ;
                }
            }
        }
        .view{
            width: 100%;
            // height: 4rem;
            // border: 1px solid red;
            margin-top: 0.5rem;
            background: white;
            
            h1{
                font-size: 0.32rem;
                margin-top: 0.3rem;
                margin-left: 0.3rem;
                margin-bottom: 0.2rem;
            }
            .imgwrap{
                width: 100%;
                // border: 1px solid red;
                overflow: hidden;
                box-sizing: border-box;
                white-space: nowrap;
                .pic-item{
                    margin-right: 0.1rem;
                    margin-bottom: 0.3rem;
                    width: 2.4rem;
                    height: 1.8rem;
                    display: inline-block;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                    &:last-child{
					margin: 0;
				    }
                }
            }
            
            
            
            
        }
        .info{
            background: white;
            margin-top: 0.5rem;
            padding-top: 0.2rem;
            h1{
                font-size: 0.32rem;
                margin-top: 0.3rem;
                margin-left: 0.3rem;
                margin-bottom: 0.2rem;
            }
            ul li{
                width: 6.7rem;
                margin-left: 0.3rem;
                font-size: 0.24rem;
                line-height: 1rem;
                // border-bottom: 1px solid #ccc; 
                border-top: 1px solid #ccc; 
                
            }
        }
    }
</style>




